import React from "react";
import { css } from "emotion";
import { FacebookShareButton } from "react-share";
import "bootstrap/dist/css/bootstrap.min.css";
import { MAP_year_to_yearName } from "../utils/mappings";
import share_arrow from "../images/share_arrow.png";

export default class SharePost extends React.Component {
  render() {
    const { row } = this.props;
    var post = `${MAP_year_to_yearName[row.year]} ${row.major} student at ${
      row.school
    }\n`;
    if (row.responseAffected) {
      post += `\n  -  ?\n${row.responseAffected}\n`;
    }
    if (row.responseCommunity) {
      post += `\n       - ?\n${row.responseCommunity}\n`;
    }
    if (row.responseDoneDifferently) {
      post += `\n         /      ?\n${row.responseDoneDifferently}`;
    }

    return (
      <FacebookShareButton
        url={"http://features.dailybruin.com/2020/unfinished-stories"}
        quote={post}
        hashtag={"UnfinishedStories"}
      >
        <div
          className={css`
            font-size: 16px;
            margin-right: 5px;
            float: left;
          `}
        >
          Share
        </div>
        {/* <FacebookIcon size={20} round={true} /> */}
        <img
          src={share_arrow}
          className={css`
            width: 16px;
            height: 16px;
            filter: brightness(150%);
          `}
        />
      </FacebookShareButton>
    );
  }
}
